.filter {
  .form-input-group {
    width: 100%;
  }
}

.filter-form-fields {
  margin-bottom: 22px;
}

.filter-form-fields .form-input {
  padding-top   : 10px;
  padding-bottom: 10px;
}

.filter-choose {
  &-item {
    position   : relative;
    // overflow: hidden;

    &-inner {
      // background    : #eee;
      margin-bottom : -10px;
      margin-top    : -10px;
      padding-top   : 10px;
      padding-bottom: 10px;
      padding-left  : 12px;
      padding-right : 12px;
      margin-right  : -12px;
      margin-left   : -12px;
      width         : calc(100% + 24px);
      border-radius : inherit;
      text-align    : center;

    }

    input {
      position: absolute;
      left    : 15px;
      top     : 10px;
      z-index : -1;
    }

    input:checked+&-inner {
      background: rgba(0, 146, 228, 0.2);
      box-shadow: 0 0 0 1px rgba(0, 146, 228, 0.8);
    }
  }
}

.filter-choose-group {
  display: flex;
  width  : 100%;

  &-item {
    flex-grow: 1;
    display  : flex;

    &+& {
      margin-left: 8px;
    }
  }

  &-num {
    max-width: 44px;
  }
}

.lk-header__btn--user {


  .btn-icon-round {
    background: rgba(242, 178, 1, 0.6);
  }
}

.header-mobile-logo .logo__img img {
  height: 30px;
}

.form-group-separator {
  align-self: center;
  color     : var(--color-text-40);
  margin    : 0 6px;
}

@media(min-width:680px) {
  .jk-item-gallery {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }

  .jk-item-gallery__preview {
    height: 46px;
  }
}

.filter-inner-cover.fixed:not(.toggle-active) .filter--inner .filter-choose-item-inner {
  padding-top   : 6px;
  padding-bottom: 6px;
  margin-top    : -6px;
  margin-bottom : -6px;
}

.jk-edit-form-btns {
  display: flex;

  .btn {
    margin-right: 15px;
  }
}

.jk-item-gallery {
  position: relative;
}

.card-control {
  position   : absolute;
  left       : 0;
  top        : 0;
  // width   : 100%;
  z-index    : 3;
  display    : flex;
}

.jk-item-gallery .card-control {
  // padding: 26px;
}

.ml-auto {
  margin-left: auto;
}

.jk-item-new-like {
  position: absolute;
  right   : 16px;
  top     : 16px;
  z-index : 3;
  cursor  : pointer;
  padding : 5px;

  .icon {
    width       : 22px;
    height      : 22px;
    fill        : none;
    stroke      : #fff;
    stroke-width: 2;
  }

  &.like-btn--active .icon {
    fill  : currentColor;
    stroke: currentColor;
  }

  &--share {
    right: 58px;

    .icon {}
  }
}

.lk-content-obj {
  .lk-control:first-of-type {
    margin-top: 0;
  }
}

.team__top {
  display: none;
}

.user-cover {
  padding-top   : 20px;
  padding-bottom: 20px;
}

.team {
  padding-bottom: 20px;
}

.jk-item-properties__info {
  // font-size: 12px;
}

.jk-item-properties {
  padding-left: 10px;
}






@media screen and (max-width:520px) {

  .jk-item-new-like {
    right     : auto;
    width     : 48px;
    height    : 48px;
    left      : 16px;
    top       : 16px;
    background: rgba(0, 0, 0, 0.4);

    display        : flex;
    align-items    : center;
    justify-content: center;
    border-radius  : 50%;

    &.like-btn--active {
      background: #fff;
    }

    &--share {
      left: 72px;
    }
  }
}

.pray--yellow {
  background: #FFE2AD;
}

@media screen and (max-width: 680px) {
  .pray--yellow .pray__img {
    background: #ffdc9b;
  }
}

.lk-content--success {
  display       : flex;
  flex-direction: column;
  flex-grow     : 1;
}

.success-page {
  display        : flex;
  flex-direction : column;
  align-items    : center;
  justify-content: center;
  text-align     : center;
  width          : 100%;
  min-height     : 100%;
  flex-grow      : 1;
}

.success-page .success-modal__btn {
  max-width: 360px;
}

.header-mobile-logo .logo__img .icon {
  height: 32px;
}

.jk-item-new-like-wrapper{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 20%;

  .jk-inner-share-list:before{
    left: calc(74% - 9px);
  }
}